import React, { useState, useEffect } from "react";
// import { Download } from "lucide-react";
import Image from "next/image";
import Trash from "../../../public/assets/Retrieve Icon.png";
import axios from "axios";
import {Undo2} from 'lucide-react'

interface Project {
  id: number;
  letter: string;
  name: string;
  category: string;
  iconBg: string;
  pinned: boolean;
}
interface Props{
  query: string;
}
export default function TrashComponent({query}: Props) {
  // const [projects, setProjects] = useState<Project[]>([]);
  const [projectData, setProjectData] = useState<any[]>([]);
  const [error, setError] = useState<string | null>(null);
  const token = localStorage.getItem("token"); // Replace with the actual token
  const colors = [
    "bg-teal-100 text-teal-600",
    "bg-rose-100 text-rose-600",
    "bg-orange-100 text-orange-600",
    "bg-blue-100 text-blue-600",
    "bg-blue-100 text-blue-600",
    "bg-orange-100 text-orange-600",
  ];

  // Fetch project data from the backend
  useEffect(() => {
    const fetchProjects = async () => {
      // console.log(token);
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/projects/trashed`,
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        let projects = response.data.projects;
        // console.log(response)
        setProjectData(projects.filter((proj: any) => proj.trashed === true));
        // setProjectData(projects);
      } catch (err) {
        if (axios.isAxiosError(err)) {
          setError(err.response?.data?.message || "Failed to fetch projects");
        } else {
          setError("Failed to fetch projects");
        }
      }
    };

    fetchProjects();
  }, []);

  const handleTrashToggle = async (project: any) => {
    try {
      const updatedTrash = !project.trashed;
      let projectId = project._id;
      console.log(projectId);
      await axios.put(
        `${process.env.NEXT_PUBLIC_API_URL}/projects/trashed`,
        { trashed: updatedTrash },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
          params: { projectId: projectId },
        }
      );

      // setProjectData((prev) => prev.map((proj) => proj.trashed = updatedTrash));
      if (updatedTrash === false) {
        setProjectData((prev) => prev.filter((proj) => proj._id !== projectId));
      }
    } catch (err) {
      setError(
        (err as any).response?.data?.message || "Failed to update Trashed status"
      );
    }
  };


  const EmptyState = () => (
    <div className="flex flex-col items-center justify-center h-[600px] bg-white rounded-lg">
      <div className="mb-4">
        <div className="bg-[#EFF1FF] rounded-full p-8 mb-4">
          <img
            src="/assets/myProjects/project.png"
            alt="No Project Found"
            className="w-24 h-24"
          />
        </div>
      </div>
      <h3 className="text-gray-700 text-xl font-sm mb-4">No Trashed Project</h3>
    </div>
  );

  if (!projectData.length) {
    return <EmptyState />;
  }

  return (
    <div className="w-full mx-auto">
      {error && (
        <div className="bg-red-100 text-red-600 p-4 rounded">
          <p>{error}</p>
        </div>
      )}
      <div className="space-y-2">
        {projectData.filter(project=> project.name.toLowerCase().includes(query.trim().toLowerCase())).map((project) => (
          <div
            key={project._id}
            className="flex items-start bg-[#ffffff] justify-between p-4 hover:bg-blue-50 rounded-lg max-w-10xl"
          >
            <div className="flex items-center">
              <div
                className={`w-10 h-10 ${
                  colors[Math.floor(Math.random()*10) % colors.length]
                } rounded-lg flex items-center justify-center font-medium`}
              >
                {project.name.charAt(0).toUpperCase()}
              </div>
              <div className="pl-6">
                <h3 className="text-sm font-medium text-gray-900">
                  {project.name}
                </h3>
                <p className="text-sm text-gray-500">{project.category}</p>
              </div>
            </div>
            <div className="flex items-center space-x-2">
              <button
                className="flex items-center p-2 bg-green-600 text-gray-400 hover:bg-green-800 border border-gray-300 rounded-lg"
                onClick={() => handleTrashToggle(project)}
              >
                <Undo2 width={20} height={20} className="text-white"/>
                <span className="ml-2 text-white">Retrieve</span>
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
