'use client'

import React from 'react';
import { useState } from 'react';
import Link from 'next/link';
import TrashComponent from '../../../../components/myProjects/TrashComponent'

export default function Trash() { 
 const [query, setQuery]= useState<string>("")
  return (
    <>
    {console.log("dashboard rendered")}
    <div className="min-h-screen bg-[#F8F7FA]">
        
      <div className="pb-6 pt-4 px-6">
        {/* Breadcrumb navigation */}
        <nav className="flex items-center space-x-2 mb-6 text-[#808080] text-sm">
          <Link href="/main/dashboard">
            <span className="cursor-pointer">Dashboard</span>
          </Link>
          <span className="text-[#808080]"></span>
          <Link href="/projects">
            <span className="cursor-pointer">My Projects</span>
          </Link>
          <span className="text-gray-400"></span>
          <span className="text-[#4d4d4d] font-medium">Trash</span> {/*No link for current page*/}
        </nav>

        {/* Search and filters container */}
        <div className="flex items-center justify-between mb-8">
          {/* Search input */}
          <div className="relative">
            <input
              type="text"
              value={query}
              placeholder="Find your project..."
              onChange={(e)=> setQuery(e.target.value)}
              className="w-80 px-4 py-2 bg-white border border-gray-200 rounded-lg"
            />
          </div>

          {/* Filter buttons */}
         
        </div>
        <TrashComponent query={query}/>
      </div>
    </div>
    </>
  );
}